/* http://meyerweb.com/eric/tools/css/reset/  v2.0 | 20110126 License: none (public domain) */
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; font-size: 100%; font: inherit; vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }

body { line-height: 1; }

ol, ul { list-style: none; }

blockquote, q { quotes: none; }

blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }

table { border-collapse: collapse; border-spacing: 0; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

html, body { width: 100%; height: 100%; }

main { min-height: 100%; }

body { margin: 0; }

canvas { display: block; position: fixed; top: 0; left: 0; bottom: 0; right: 0; cursor: cell; background: #000; transition: background-color 200ms ease-in-out; }

canvas.light { background: #fff; }

/* Quick fix for a broken DAT.GUI style */
.hue-field { width: 13px !important; }

.npm-scb-wrap.open .npm-scb-now-playing { opacity: 0; }

.npm-scb-wrap.open .npm-scb-inner { top: 0; }

.track { margin: 1em; }

.cam-stream { position: absolute; left: 1em; bottom: 1em; }

.controls { position: fixed; display: flex; flex-flow: column nowrap; justify-content: stretch; top: 0; right: 0; max-height: 100%; padding-bottom: 20px; z-index: 1000; opacity: 0; visibility: hidden; transition: opacity 300ms ease-out; }

.controls.show { opacity: 1; visibility: visible; }

.controls .dg.main { overflow-y: auto; }

.controls .close-button { position: absolute; bottom: 0; }
